	export default {
		data() {
			let self = this;
			return {
				flags: true,
				flag: true,
				title: {
					name: "riskavoidance",
					flag: false
				},
				attalist: [],
				zoom: 12,
				currPosition: [113.563853, 22.141086],
				//center: [116.23774, 40.07595],//北京
				center: [113.545465, 22.173764], //澳門
				markers: [],
				reObjArr1: [], //上报数据总和
				reObjArr2: [], //预警数据总和
				reObjArr3: [] //上报和预警数据总和
					,
				pointArr: [] //
					,
				map: {},
				currmarker: {},
				geolocation:null,
				// requestUrl: "",
				isAndroid: false
			};
		},
		mounted() {
			this.map = new AMap.Map("container", {
				zoom: 12,
				center: [113.545465, 22.173764] //澳門
					,
				resizeEnable: true,
			});
			this.ajax();
			console.log("param==>run success");
			window.setI18nValue = this.$setI18nValue; //国际化赋值
			window._showImage= function(e){
				let src=e.srcElement.src;
				wx.previewImage({
					current: src, // 当前显示图片的http链接
					urls: [src] // 需要预览的图片http链接列表
					});
			}
			setTimeout(this.initPosition,300);
		},
		created() {
			// this.requestUrl = this.$myConfig.ip + ":" + this.$myConfig.port;
			this.isAndroid = this.isAndroid_ios();
			if (this.isAndroid) {
				window.android.enableJsBackPressed();
			} else {
				console.log("is ios")
			}

		},
		methods: {
			ajax() {
				let self = this;
				self.$vux.loading.show({text: self.$t('loading')})//打開正在加載
				var qurl = window.g.test_getRefugeeDatas || window.g.getRefugeeDatas;
				var method = window.g.test_getRefugeeDatas?'GET':'POST';
				//qurl="http://202.175.19.182:8080/rms/shelter/list"
				var param = {
					"key": self.$myConfig.key,
					"status": "",
					"name": "",
					"address": "",
					"centerCode": "",
					"organizationID": "",
					"isTransform": "1"
				}
				self.$commonAjax(method, qurl, param, function(data) {
					console.log("come kp")
					$(".spinDiv").find(">img").removeClass("refresh-animation")
					self.$vux.loading.hide(); //關閉正在加載
					if (data.data != "") {
						/* console.log(data.data) */
						var jsondata = JSON.parse(data.data);
						console.log(jsondata)
						var data = jsondata;
						var obj;
						self.pointArr = [];
						for (var i = 0; i < data.length; i++) {
							obj = new Object()
							var chobj = data[i].placeModel;
							obj.code = chobj.code; //ID
							obj.districtName = chobj.districtName; //行政区划
							obj.dutyPhone = chobj.dutyPhone; //值班电话
							obj.name = chobj.name; //场所名称
							obj.sname = chobj.sname; //场所名称
							obj.ename = chobj.ename; //场所名称
							obj.pname = chobj.pname; //场所名称
							obj.resourceCatalogName = self.changeNull(chobj.resourceCatalogName);//场所所属类型
							obj.sresourceCatalogName = self.changeNull(chobj.sresourceCatalogName);
							obj.presourceCatalogName = self.changeNull(chobj.presourceCatalogName);
							obj.eresourceCatalogName = self.changeNull(chobj.eresourceCatalogName);
							obj.longitude = chobj.longitude; //经度
							obj.latitude = chobj.latitude; //纬度
							obj.capacity = data[i].capacity; //可容納人數
							obj.placedNumber = data[i].placedNumber; //已容納人數
							obj.imgUrl = chobj.photo;
							obj.type = data[i].type;
							obj.showNumber=data[i].showNumber;
							obj.address=chobj.address||"";
							obj.onOffFlag = chobj.onOffFlag;

							let getImage=window.g.getRefugeeImage;
							if (getImage) {
								obj.imgUrl=getImage.replace(':id',
									chobj.photo.substr(chobj.photo.lastIndexOf('/')+1)
								);
							}

							self.pointArr.push(obj)
						}
						self.sajax(4);
					} else {
						self.$showBottomToast(self.$t('nodata'), 'bottom');
						self.sajax(4);
					}
				}, function() {
					//self.showBottomToast("请求数据失败！")
					// var appData = require("../../assets/json/avoidriskData.json");
					// console.log(appData);
					// var data = appData.data;
					// var obj;
					self.pointArr = [];
					// for (var i = 0; i < data.length; i++) {
					// 	obj = new Object()
					// 	var chobj = data[i].placeModel;
					// 	obj.code = chobj.code; //ID
					// 	obj.districtName = chobj.districtName; //行政区划
					// 	obj.dutyPhone = chobj.dutyPhone; //值班电话
					// 	obj.name = chobj.name; //场所名称
					// 	obj.sname = chobj.sname; //场所名称
					// 	obj.ename = chobj.ename; //场所名称
					// 	obj.pname = chobj.pname; //场所名称
					// 	obj.resourceCatalogName = self.changeNull(chobj.resourceCatalogName); //场所所属类型
					// 	obj.sresourceCatalogName = self.changeNull(chobj.sresourceCatalogName);
					// 	obj.presourceCatalogName = self.changeNull(chobj.presourceCatalogName);
					// 	obj.eresourceCatalogName = self.changeNull(chobj.eresourceCatalogName);
					// 	obj.longitude = chobj.longitude; //经度
					// 	obj.latitude = chobj.latitude; //纬度
					// 	obj.capacity = data[i].capacity; //可容納人數
					// 	obj.placedNumber = data[i].placedNumber; //已容納人數
					// 	obj.imgUrl = chobj.photo;
					// 	obj.type = data[i].type;
					// 	self.pointArr.push(obj)
					// }
					self.$vux.loading.hide(); //關閉正在加載
					$(".spinDiv").find(">img").removeClass("refresh-animation")
					self.sajax(4);
				}, true)
				/**/
				//self.reObjArr3 = self.reObjArr1.concat(self.reObjArr2);
			},
			changeNull(val) {
				var resultStr = "";
				if (val == undefined || val == null || val == 'null') {
					resultStr = "";
				} else {
					resultStr = val;
				}
				return resultStr;
			},
			setPosition() {
				if(this.geolocation) {
					this.geolocation.getCurrentPosition(function(status,result){
						if(status=='complete'){
							__gs_onComplete(result)
						}else{
							__gs_onError(result)
						}
					});
				}
			},
			initPosition() {
				let that=this;
				AMap.plugin('AMap.Geolocation', function() {
					var geolocation = new AMap.Geolocation({
						enableHighAccuracy: true,//是否使用高精度定位，默认:true
						timeout: 10000,          //超过10秒后停止定位，默认：5s
						buttonPosition:'RB',    //定位按钮的停靠位置
						buttonOffset: new AMap.Pixel(10, 20),//定位按钮与设置的停靠位置的偏移量，默认：Pixel(10, 20)
						zoomToAccuracy: true,   //定位成功后是否自动调整地图视野到定位点
			
					});
					that.map.addControl(geolocation);
					that.geolocation=geolocation;
				});
				//解析定位结果
				window.__gs_onComplete= (data) => {
				}
				//解析定位错误信息
				window.__gs_onError= (data) => {
				}
			},
			refreshData() {
				console.log("刷新")
				$(".spinDiv").find(">img").addClass("refresh-animation");
				this.map.remove(this.markers);
				this.locationCenter();
				this.ajax();
			},
			jumpTel() {
				this.$router.push({
					path: "/query/query",
					query: {}
				});
			},
			showBottomToast(val) {
				// 显示
				this.$showBottomToast(val, 'bottom')
			},
			sajax(index) {
				console.log("come in sajax!!!")
				let self = this;
				let cluster = [];
				let markers = [];
				let reObjArr = [];
				//console.log(self.pointArr);
				if (index == 1) {
					//上报
					reObjArr = self.reObjArr1;
				}
				if (index == 2) {
					//预警
					reObjArr = self.reObjArr2;
				}
				if (index == 3) {
					reObjArr = self.reObjArr3;
				}
				if (index == 4) {
					//避险点位
					reObjArr = self.pointArr;
				}
				var infoWindow = new AMap.InfoWindow({
					offset: new AMap.Pixel(0, -10),
					closeWhenClickMap: true
				});
				for (let i = 0; i < reObjArr.length; i++) {
					var obj = reObjArr[i];
					if (!self.checkRate(obj.longitude)) {
						continue;
					}
					//实心点：background: hsla(180, 100%, 50%, 0.7); height: 24px; width: 24px; border: 1px solid hsl(180, 100%, 40%); border-radius: 12px; box-shadow: hsl(180, 100%, 50%) 0px 0px 1px;
					//height: 25px;width: 25px;background:url(//webapi.amap.com/theme/v1.3/images/newpc/way_btn2.png);background-size: 30px;background-position-y: 1.02rem;
					//var htmlstr='<div style="height: 25px;width: 25px;background:url(//webapi.amap.com/theme/v1.3/images/newpc/way_btn2.png);background-size: 30px;background-position-y: 1.02rem;"></div>'
					var htmlstr = '<div style="height: 30px;width: 30px;background:url(' + require("../../assets/images/mk.png") +
						');background-size: 30px;"></div>'
					// var htmlstr_jjtkd =
					// 	'<div style="height: 25px;width: 25px;background:url(//webapi.amap.com/theme/v1.3/images/newpc/way_btn2.png);background-size: 30px;background-position-y: 1.02rem;"></div>'

					var iconUrl = "";
					if (obj.type == '1') {
						if(obj.onOffFlag) // 1 open 0 off
							iconUrl = require("../../assets/images/icon/mkb.png");
						else
							iconUrl = require("../../assets/images/icon/mkb_off.png");
					} else {
						if(obj.onOffFlag)
							iconUrl = require("../../assets/images/icon/mky.png");
						else 
							iconUrl = require("../../assets/images/icon/mky_off.png");
					}
					var marker = new AMap.Marker({
						position: [obj.longitude, obj.latitude],
						icon: iconUrl,
						/* content:htmlstr */
					});
					marker.content = "<div></div>";
					marker.on("click", function(e) {
						infoWindow.close();
						infoWindow.setContent(e.target.content);
						let postionObj = [];
						/* var zoom=map.getZoom();
						let clat=0.000001*(zoom+zoom*4);
						console.log(e.target.getPosition()) */
						postionObj = [(e.target.getPosition().lng), e.target.getPosition().lat];
						infoWindow.open(self.map, postionObj);

						self.attalist = [];
						var showHtml = `<div class="prompt" style="width:300px"><ul class="mui-table-view">`;
						for (var i = 0; i < self.pointArr.length; i++) {

							var eposition = e.target.getPosition();
							var longflag = self.changePostion(self.pointArr[i].longitude) == self.changePostion(eposition.lng)
							var latflag = self.changePostion(self.pointArr[i].latitude) == self.changePostion(eposition.lat)
							//console.log(self.pointArr[i])
							if (
								longflag && latflag
							) {
								console.log(self.pointArr[i])
								self.attalist.push(self.pointArr[i]);
								var obj = self.pointArr[i];
								var nameStr = "";
								let str = self.$i18n.locale;
								if (str == 'zh-CN') {
									nameStr = obj.sname;
								} else if (str == 'en-US') {
									nameStr = obj.ename == null ? obj.sname : obj.ename;
								} else if (str == 'pt-PT') {
									nameStr = obj.pname == null ? obj.sname : obj.pname;
								}
								obj.placedNumber = obj.placedNumber == null ? "0" : obj.placedNumber;
								obj.capacity = obj.capacity == null ? "0" : obj.capacity;
								showHtml +=
									`<li data-repid="${obj.code}" data-flag="${
                obj.flag
              }" class="mui-table-view-cell mui-media">`;
								showHtml += `<a>`; // href="javascript:void(0);"
								showHtml += `<div class="mui-media-body">`
								if (obj.imgUrl) {
									showHtml += `	<img style="width: 300px;height:180px" onclick='javascript:_showImage(event);' src="${obj.imgUrl}"/><br/>`
								}
								showHtml += `<p style="white-space: normal">` + self.$t('name') + `:` + nameStr + `
                          </p><p style="white-space: normal">` 
                          	if (str == 'zh-CN') {
                          		if(obj.sresourceCatalogName.length > 0){
                          			showHtml += self.$t('type') + `:${obj.sresourceCatalogName}</p>`
                          		}else{
                          			showHtml += self.$t('type') + `:${obj.resourceCatalogName}</p>`
                          		}
							} else if (str == 'en-US') {
								if(obj.eresourceCatalogName.length > 0){
                          			showHtml += self.$t('type') + `:${obj.eresourceCatalogName}</p>`
                          		}else{
                          			showHtml += self.$t('type') + `:${obj.resourceCatalogName}</p>`
                          		}
							} else if (str == 'pt-PT') {
								if(obj.presourceCatalogName.length > 0){
                          			showHtml += self.$t('type') + `:${obj.presourceCatalogName}</p>`
                          		}else{
                          			showHtml += self.$t('type') + `:${obj.resourceCatalogName}</p>`
                          		}
							}
                          showHtml += `<p class="mui-ellipsis">` +
									self.$t('phonenum') + `:${obj.dutyPhone}</p>`;
									if(obj.type=='1'){
										showHtml+=`<p style="white-space: normal">` + self.$t('placedNumber') +`/` + self.$t('capacity') +`:${obj.placedNumber}/${obj.capacity}</p>`
									}else{
										console.log("data==>"+obj.showNumber)
									}
                          showHtml+=`</div>
                            </a></li>`;
							}
						}
						showHtml += `</ul></div>`;

						$(".amap-info-content.amap-info-outer").html(showHtml);
						$(".prompt ul li").on("click", function() {
							/*self.clust(
							  this.getAttribute("data-repid"),
							  this.getAttribute("data-flag")
							);*/
						});
						//infoWindow.open(map, e.target.getPosition());
					});
					markers.push(marker);
					//debugger;
					//console.log(marker.getPosition())
					//marker.setPosition(ll)

				}
				//84坐标转换成高德坐标
				/* var handlemarkers=[];
				for(var m=0;m<markers.length;m++){
					var mobj=markers[m];
					var ll=mobj.getPosition()
					handlemarkers.push(ll);
				}
				AMap.convertFrom(handlemarkers, "gps",function(status,result){
					console.log(status)
					console.log(result)
					for(var m=0;m<result.locations.length;m++){
						var newmarker=markers[m];
						//console.log(newmarker.getPosition())
						newmarker.setPosition(result.locations[m])
						//console.log(newmarker.getPosition())
						self.pointArr[m].longitude=newmarker.getPosition().lng;
						self.pointArr[m].latitude=newmarker.getPosition().lat;
					}
					map.add(markers)
				}) */
				self.map.add(markers)

				AMap.plugin(["AMap.ToolBar", "AMap.Scale"], function() {
					self.map.addControl(new AMap.ToolBar());
					self.map.addControl(new AMap.Scale());
				});

				self.markers = markers;
				// self.setPosition();
				//map.setCenter(centerObj)
				/* setTimeout(()=>{
				 	console.log("time out")
				 	map.panTo(centerObj)
				 },2000)*/

			},
			changePostion(val) {
				val = val + "";
				if (val.length > 8) {
					val = val.substr(0, 8)
				}
				return val;
			},
			checkRate(val) {
				var re = /^[0-9]+.?[0-9]*$/;
				return re.test(val);
			},
			locationCenter() {
				var _this = this;
				_this.map.setZoomAndCenter(12, _this.center)
			},
			isAndroid_ios() {
				//android is true else is false
				var u = navigator.userAgent,
					app = navigator.appVersion;
				var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1; //android终端或者uc浏览器  
				var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端  
				return isAndroid == true ? true : false;
			}
		}
	};



